*, h1, h2, p {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding-left: 40px;
  padding-right: 40px;
  display: flex;
  overflow-x: hidden;
}

.container {
  margin: 300px;
}

.select-container {
  margin-top: 20px;
}

.breed-select {
  width: 150px;
  cursor: pointer;
  margin-bottom: 10px;
  padding: 4px;
}

.box {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.cat-info {
  gap: 30px;
  display: flex;
}

.text-container {
  flex-direction: column;
  justify-content: space-between;
  gap: 16px;
  display: flex;
}

.title, .text, .subtitle {
  color: #22343d;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: normal;
}

.text {
  font-size: 16px;
}

.subtitle {
  font-size: 22px;
  font-weight: 600;
}

.error {
  color: red;
  font-size: 16px;
  font-weight: 500;
}

.is-hidden {
  display: none;
}

.loader-container {
  position: relative;
}

.loader {
  width: 86px;
  height: 50px;
  animation: flippx 1s linear infinite;
  position: relative;
}

.loader:before {
  content: "";
  width: 20px;
  height: 20px;
  transform-origin: -14px;
  background: #ff3d00;
  border-radius: 50%;
  margin: auto;
  animation: spin .5s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes flippx {
  0%, 49% {
    transform: scaleX(1);
  }

  50%, 100% {
    transform: scaleX(-1);
  }
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

